import React from 'react'


const SalaryStructure = () => {
  return (
      <div className="col-md-5 mb-3 pe-1 ">
      <div className="cardBox h-100">
        <div className="cardHead">
          <h3 className="d-flex align-items-center gap-2">Salary Structure <button type="button" className="viewBtn"><i className="bi bi-eye" /></button>
          </h3>
        </div>
        <div className="row px-md-1">
          <div className="col-md-6 px-md-2 mb-3">
            <div className="salaryInfo">
              <strong>Take Home</strong>
              <p>Rs <b>100000</b></p>
            </div>
          </div>
          <div className="col-md-6 px-md-2 mb-3">
            <div className="salaryInfo">
              <strong>Annual CTC</strong>
              <p>Rs <b>150000</b></p>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-md-6 mb-3">
            <div className="salaryEarning">
              <strong>Earnings</strong>
              <p>15000</p>
            </div>
          </div>
          <div className="col-md-6 mb-3">
            <div className="salaryDeduction">
              <strong>Deduction</strong>
              <p>2000</p>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-md-6">
            <div id="salaryChart" style={{height: '188px', width: '170px'}} />
          </div>
          <div className="col-md-6">
            <div className="salaryDetail">
              <div className="d-flex align-items-center">
                <figure>
                  <img src="images/arrears-icon.svg" alt="" />
                </figure>
                <p>Arrears
                  <b>1200</b>
                </p>
              </div>
              <div className="d-flex align-items-center">
                <figure>
                  <img src="images/advance-icon.svg" alt="" />
                </figure>
                <p>Advance
                  <b>1000</b>
                </p>
              </div>
              <div className="d-flex align-items-center">
                <figure>
                  <img src="images/loan-icon.svg" alt="" />
                </figure>
                <p>Total Loan/Pending Loan
                  <b>100000/50000</b>
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default SalaryStructure