import React from 'react'
import { PayrollMonthlyStatus, SalaryStructure,SelectPayroll } from '../components/payroll'


const PayrollDashboard = () => {
  return (
      <>
        <div className="cardBox mypayroll mb-3">
            <PayrollMonthlyStatus/>
      </div>
      <div className='row'>
      <SalaryStructure/>
      <SelectPayroll/>
      </div>
      </>
    
  )
}

export default PayrollDashboard