import React from 'react'
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Button from '../../components/commanComponent/Button'
import Image from '../../components/commanComponent/Image'
import {ViewSalaryStatement} from '../../components/modal/payrollManagementSystem/Index'


const SelectPayroll = () => {
      var settings = {
            dots: false,
            infinite: true,
            speed: 500,
            slidesToShow: 4,
            slidesToScroll: 2
          };
  return (
    
      <div className="col-md-7 mb-3">
        <div className="cardBox h-100 overflow-hidden">
          <div className="cardHead d-flex justify-content-between align-items-center">
            <h3 className="d-flex align-items-center gap-2">Payroll <button type="button" className="viewBtn"><i className="bi bi-eye" /></button></h3>
            <div className="d-flex dateFilterSelect gap-2">
              <select className="form-control quarterSelect form-select">
                <option>Quarter 1 (Apr - Jun)</option>
                <option>Quarter 1 (Apr - Jun)</option>
                <option>Quarter 1 (Apr - Jun)</option>
              </select>
              <select className="form-control form-select">
                <option>2023</option>
                <option>2022</option>
                <option>2021</option>
              </select>
            </div>
          </div>
          <div className="quarterSlide">
          <Slider {...settings}>
            <div className="slickItem">
              <div className="quarterBox">
                <h4>Quarter 1</h4>
                <p>April <b>2023</b></p>
                <div className="paySlip">
                  Payslip: <span> Available</span>
                </div>
              </div>
            </div>
            <div className="slickItem">
              <div className="quarterBox">
                <h4>Quarter 1</h4>
                <p>April <b>2023</b></p>
                <div className="paySlip">
                  Payslip: <span> Available</span>
                </div>
              </div>
            </div>
            <div className="slickItem">
              <div className="quarterBox">
                <h4>Quarter 1</h4>
                <p>April <b>2023</b></p>
                <div className="paySlip">
                  Payslip: <span> Available</span>
                </div>
              </div>
            </div>
            <div className="slickItem">
              <div className="quarterBox">
                <h4>Quarter 1</h4>
                <p>April <b>2023</b></p>
                <div className="paySlip">
                  Payslip: <span> Available</span>
                </div>
              </div>
            </div>
            <div className="slickItem">
              <div className="quarterBox">
                <h4>Quarter 1</h4>
                <p>April <b>2023</b></p>
                <div className="paySlip">
                  Payslip: <span> Available</span>
                </div>
              </div>
            </div>
            <div className="slickItem">
              <div className="quarterBox">
                <h4>Quarter 1</h4>
                <p>April <b>2023</b></p>
                <div className="paySlip">
                  Payslip: <span> Available</span>
                </div>
              </div>
            </div>

            </Slider>
          
          </div>
          <div className="payrolSalaryTxt row">
            <div className="col">
              <span>Gross Pay :</span>
              <p>15,000</p>
            </div>
            <div className="col">
              <span>Deductions :</span>
              <p>2,000</p>
            </div>
            <div className="col d-flex gap-2 align-items-center">
              <div>
                <span>Total Pay</span>
                <p>13,000</p>
              </div>
              <div className="d-flex infotxt">
                <i className="bi bi-caret-up-fill" />
                <p><b>10%</b> more than previous month</p>
              </div>
            </div>
          </div>
          <div className="payrolBtnGroup">
            <Button btnClassName={"viewPayslipBtn"} dataBsTarget={"#viewPaySlipModal"} dataBsToggle={"modal"}>
              <Image imgSrc={"images/payslip-view.svg"} altText={""} width={25}></Image>View Payslip
            </Button>
            {/* <button type="button" className="viewPayslipBtn" data-bs-target="#viewPaySlipModal" data-bs-toggle="modal"><img src="images/payslip-view.svg" alt="" width={25} /> View
              Payslip</button> */}
            <button type="button" className="downloadPayslipBtn"><img src="images/payslip-download.svg" alt="" width={25} /> Download Payslip</button>
          </div>
        </div>
        <ViewSalaryStatement/>
      </div>      
  )
}

export default SelectPayroll